        import React, { useEffect, useState } from "react";
        import { Splide, SplideTrack } from "@splidejs/react-splide";
        import "../../../analytics-new/assets/css/slide.scss";
        import '@splidejs/react-splide/css';
 
        const CreatedWalls = ({ setactiveWall, section, totalRecord, splideRef, children}) => {

        const [currentSlide, setCurrentSlide] = useState(0);
 
        useEffect(() => {

          if(section && setactiveWall){

                 const splideInstance = splideRef.current.splide;

                //  splideInstance.on('click', (e) => {
                //     if(e.slide.className.includes('is-active')){

                //         const wallId =  e.slide.getAttribute('data-wallId')
                //         const feedList =  e.slide.getAttribute('data-feedList')
                //        // nevigationRedirect(wallId,4,feedList);

                //         }else{
                //         splideRef.current.splide.go(e.index); // Move to the clicked slide
                //      }
                //   });
                 
                 splideInstance.on('move', (newIndex) => {
                    setCurrentSlide(newIndex);
                  });
                    
                if (splideRef.current && totalRecord>1) {
                  splideRef.current.splide.on("move", (newIndex) => {
                    manageSlider(newIndex)
                    setactiveWall(newIndex)
                 });
              }else{
                manageSlider()
                setactiveWall(1)
              }
          }
        }, []);

      
        const manageSlider=(index=null)=>{
  
            const totalList = totalRecord * 2;   
            const newIndexCondition = totalRecord-1;

            const toggleDisplay = (displayStyle) => {

                const slideWalls = document.getElementsByClassName(`splide__slide_walls`)

                for (let i = (totalRecord+1); i <= totalList; i++) {
                  if(slideWalls && slideWalls.length){
                      let updateKey = totalRecord-1
                      let elementNew = document.getElementById(slideWalls[i+updateKey].id);
                       if (elementNew) elementNew.style.display = displayStyle;
                  }
                }
            };

            if (index === newIndexCondition || index==null) {
                if(index==null){
                   const Ispreview = document.getElementsByClassName(`is-prev`)
                   const splideListUl = document.getElementsByClassName(`splide__list_ul`)
                   const Isnext = document.getElementsByClassName(`is-next`)

                    if(Ispreview.length) Ispreview[0].style.display="none"
                    if(Isnext.length) Isnext[0].style.display="none"
                    if(splideListUl) splideListUl[0].querySelector('ul').style.transform ="translateX(0)";
                 }else{
                    toggleDisplay('none');
                }
             } else {
                toggleDisplay('block');
            }
        }

        const settings = {
            start: 0,
            type: "loop",
           //rewind: true,
            speed: 1000,
            perMove: 1,
            loop: true,
            interval: 5000,
            drag: section==1 ? false : true,
            //clones: 1,
            perPage: section==1 ? 0 : totalRecord>3 ? 3 : totalRecord,
            autoWidth: section==1 ? true : false,
            gap: section==1 ? 15 : -15,
            autoplay: section==1 ? false : true,
            pagination: false,
            arrows: section === 1 ? totalRecord !== 1 : true, 
            fixedHeight: true,
            breakpoints: {
                0: {
                    perPage: 1
                },
                579: {
                    perPage: 2
                },
                767: {
                    perPage: 2,
                },
                1300: {
                    perPage: 2,
                },
                1600: {
                    perPage: 3,
                }
            }
        };
          

        return (
            <Splide  className={section ? 'splide__list_ul' : ''} ref={splideRef} hasTrack={false} options={settings}>
                <SplideTrack>
                 {children}</SplideTrack>

                 <div className="splide__arrows splide__arrows--ltr tb_g_h_arrow_wrapper_" role="navigation" aria-label="Slider Navigation Wrapper">
                    <div role="navigation" className="tb_g_h_arrow splide__arrow splide__arrow--prev tb_g_h_arrow_left__ tb__icon tb-arrow-left-alt" aria-labelledby="Navigation Previous Slide"> <button disabled={currentSlide === 0 && section==1}><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor"><path d="M10.8284 12.0007L15.7782 16.9504L14.364 18.3646L8 12.0007L14.364 5.63672L15.7782 7.05093L10.8284 12.0007Z"></path></svg></button></div>
                    <div role="navigation" className="tb_g_h_arrow splide__arrow splide__arrow--next tb_g_h_arrow_right__ tb__icon tb-arrow-right-alt" aria-labelledby="Navigation Next Slide"> <button disabled={currentSlide === totalRecord - 1 && section==1}><svg xmlns="http://www.w3.org/2000/svg" width={"20px"} height={"20px"} viewBox="0 0 24 24" fill="currentColor"><path d="M13.1717 12.0007L8.22192 7.05093L9.63614 5.63672L16.0001 12.0007L9.63614 18.3646L8.22192 16.9504L13.1717 12.0007Z"></path></svg> </button></div>
                </div> 
            </Splide>
         );
       };

 export default CreatedWalls;
